@mixin d($dispositivo) {
  @if $dispositivo == t {
    @media only screen and (min-width: 768px) and (max-width: 959px) {
      @content;
    }
  } @else if $dispositivo == m {
    @media only screen and (max-width: 767px) {
      @content;
    }
  }
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

$container: 1308;
$gutter: 20;
$colunas: 16;

%grid {
  float: left;
  margin-left: ($gutter / 2) + px;
  margin-right: ($gutter / 2) + px;
}

@for $i from 1 through $colunas {
  $width: $container / $colunas * $i - $gutter;
  .grid-#{$i} {
    @extend %grid;
    width: $width + px;
  }
}

.grid-1-3 {
  @extend %grid;
  width: ($container / 3) - $gutter + px;
}

.container {
  width: $container + px;
  margin: 0 auto;
  padding: 0;
  position: relative;
}

.container:after,
.container:before {
  content: " ";
  display: table;
}

.container:after {
  clear: both;
}

@include d(t) {
  $container: 768;

  @for $i from 1 through $colunas {
    $width: $container / $colunas * $i - $gutter;
    .grid-#{$i} {
      width: $width + px;
    }
  }

  .grid-1-3 {
    width: ($container / 3) - $gutter + px;
  }

  .container {
    width: $container + px;
  }
}

@include d(m) {
  $container: 100%;

  %mobile {
    width: 100%;
    margin: 0 0 20px 0;
    float: none;
  }

  @for $i from 1 through $colunas {
    .grid-#{$i} {
      @extend %mobile;
    }
  }

  .grid-1-3 {
    @extend %mobile;
  }

  .container {
    width: $container;
  }
}
